import { useQuery, gql } from "@apollo/client";
import { Box, Spinner, Text, VStack } from "@chakra-ui/react";
import type { ReactElement, JSXElementConstructor, ReactNode, Key, ReactPortal } from "react";

const GET_COUNTRIES = gql`
  query GetCountries {
    countries {
      code
      name
    }
  }
`;

const CountriesList = () => {
  const { loading, error, data } = useQuery(GET_COUNTRIES);

  if (loading) return <Spinner size="xl" />;
  if (error) return <Text color="red.500">Error: {error.message}</Text>;

  return (
    <VStack spacing={4} align="stretch" p={4}>
      {data.countries.map((country: { code: boolean | ReactElement<any, string | JSXElementConstructor<any>> | Iterable<ReactNode> | Key | null | undefined; name: string | number | boolean | ReactElement<any, string | JSXElementConstructor<any>> | Iterable<ReactNode> | ReactPortal | null | undefined; emoji: string | number | boolean | ReactElement<any, string | JSXElementConstructor<any>> | Iterable<ReactNode> | ReactPortal | null | undefined; }) => (
        <Box
          p={4}
          borderWidth="1px"
          borderRadius="lg"
          _hover={{ bg: "gray.100" }}
        >
          <Text fontSize="lg" fontWeight="bold">
            {country.name}
          </Text>
        </Box>
      ))}
    </VStack>
  );
};

export default CountriesList;
